import React, { Component } from "react";
import { Icon, Tooltip, Modal, Dropdown, Button, Menu } from "antd";
import { inject, observer } from "mobx-react";
import { withTranslation } from "react-i18next";
import "./style.scss";
import Checkbox from "../../common/Widget/Checkbox";
import { trim } from "../../util/index";
import { get } from "mobx";
import { host } from "../../ws";

const { confirm } = Modal;

@withTranslation()
@inject("login", "rahmen", "playlist")
@observer
class Frameuser extends Component {
  state = {
    visible: false,
    defaultVal: "",
    user_id: null,
  };

  show(val1, val2, id) {
    let name = val1 || val2;
    this.setState({ visible: true, defaultVal: name, user_id: id });
  }

  handlecancel = () => {
    this.setState({ visible: false, defaultVal: "", user_id: null });
  };

  handleconfirm = (name) => {
    const newName = trim(name);
    this.props.rahmen.frame_user_rename(
      this.props.node,
      this.state.user_id,
      newName
    );
    this.setState({ visible: false, defaultVal: "", user_id: null });
  };

  //设为管理员
  setAdmin(id) {
    this.props.rahmen.frame_user_admin(this.props.node, id, "true");
  }
  //取消管理员
  cancelAdmin(id) {
    this.props.rahmen.frame_user_admin(this.props.node, id, "false");
  }
  //删除用户
  user_delete = (id) => {
    confirm({
      title: "Are you sure to delete this user?",
      okText: "Yes",
      okType: "danger",
      cancelText: "No",
      onOk: () => {
        this.props.rahmen.frame_user_delete(this.props.node, id);
      },
      onCancel() {
        console.log("Cancel");
      },
    });
  };
  render() {
    const { t, visible, cancel, inner } = this.props;
    const { frame_user_list } = this.props.rahmen;
    const { user_info } = this.props.login;

    return (
      <>
        <Modal
          title={t('UserList')}
          visible={visible}
          getContainer={false}
          onCancel={cancel}
          footer={null}
          width={inner ? "100%" : 800}
          className="frameuser"
          centered
        >
          <div className="frameuser-bd">
            <ul>
              {frame_user_list.map((item, i) => {
                const menu = (
                  <Menu>
                    <Menu.Item>
                      {get(item, "is_admin") ? (
                        <span
                          onClick={() => {
                            this.cancelAdmin(item.id);
                          }}
                        >
                          {t("cancelAdmin")}
                        </span>
                      ) : (
                        <span
                          onClick={() => {
                            this.setAdmin(item.id);
                          }}
                        >
                          {t("setAdmin")}
                        </span>
                      )}
                    </Menu.Item>
                    <Menu.Item>
                      <span
                        onClick={() => {
                          this.show(item.nickname, item.name, item.id);
                        }}
                      >
                        {t("modifyNickname")}
                      </span>
                    </Menu.Item>
                    {item.id !== user_info.id && (
                      <Menu.Item>
                        <span
                          onClick={() => {
                            this.user_delete(item.id);
                          }}
                        >
                          {t("delete")}
                        </span>
                      </Menu.Item>
                    )}
                  </Menu>
                );
                return (
                  <li key={item.id}>
                    <span
                      className="user-icon"
                      style={
                        get(item, "profile")
                          ? {
                              backgroundImage: `url(${host()}/uf/s/${
                                get(item, "profile").id
                              }/${item.id})`,
                            }
                          : {}
                      }
                    >
                      {!get(item, "profile") && <Icon type="user" />}
                    </span>
                    <Tooltip
                      title={item.nickname ? item.nickname : item.name}
                      overlayClassName="tooltip"
                    >
                      <span className="user-name ellipsis">
                        {item.nickname ? item.nickname : item.name}
                      </span>
                    </Tooltip>
                    <span
                      className="user-is-admin"
                      style={
                        get(item, "is_admin") ? {} : { visibility: "hidden" }
                      }
                    >
                      {t("Admin")}
                    </span>
                    <div className="frameuser-operation">
                      {get(item, "is_admin") ? (
                        <span
                          className="operation-admin"
                          style={
                            item.id === user_info.id
                              ? { display: "none" }
                              : {}
                          }
                          onClick={() => {
                            this.cancelAdmin(item.id);
                          }}
                        >
                          {t("cancelAdmin")}
                        </span>
                      ) : (
                        <span
                          className="operation-admin"
                          style={
                            item.id === user_info.id
                              ? { display: "none" }
                              : {}
                          }
                          onClick={() => {
                            this.setAdmin(item.id);
                          }}
                        >
                          {t("setAdmin")}
                        </span>
                      )}

                      <span
                        className="user-rename"
                        onClick={() => {
                          this.show(item.nickname, item.name, item.id);
                        }}
                      >
                        {t("modifyNickname")}
                      </span>
                      <span
                        className="user-delete"
                        style={
                          item.id === user_info.id
                            ? { display: "none" }
                            : {}
                        }
                        onClick={() => {
                          this.user_delete(item.id);
                        }}
                      >
                        {t("delete")}
                      </span>

                      <Dropdown overlay={menu} className="more_user">
                        <Button type="primary" icon="ellipsis" />
                      </Dropdown>
                    </div>
                  </li>
                );
              })}
            </ul>
          </div>
        </Modal>

        {this.state.visible ? (
          <Checkbox
            style={{ zIndex: 9999 }}
            title={t("modifyNickname")}
            defaultValue={this.state.defaultVal}
            handlecancel={this.handlecancel}
            handleconfirm={this.handleconfirm}
          />
        ) : null}
      </>
    );
  }
}

export default Frameuser;
