import React, { PureComponent } from "react";
import { Tooltip, Menu, Dropdown, Icon } from "antd";
import Dropzone from "react-dropzone";
import { withTranslation } from "react-i18next";
import imgload2 from "../images/imgerror.png";
import { getFile, os } from "../util/index";
import dayjs from "dayjs";
import { get } from "mobx";
import "./index.scss";
import { color } from "../progresscolor";

/**
 *  acceptedFiles //拖拽上传的方法
 */

@withTranslation()
class ListCell extends PureComponent {
  render() {
    const {
      t,
      title,
      item,
      name,
      style,
      goPlaylistdetails,
      album,
      oncheck,
      timeName,
      time,
      morelist,
      click,
      frameNumDis,
      framePlay,
      acceptedFiles,
      sortindex,
      selectNum,
    } = this.props;

    try {
      if (item.true) {
        return <div style={style}></div>;
      } else {
        let menu;
        const ti = sortindex === 1 ? item.created : item.updated || item.created;
        if(morelist){
          menu = (
            <Menu
              onClick={(e) => {
                click(e, item);
              }}
            >
              {morelist.map((el, i) => {
                if (item) {
                  if (item.frame_default && el.icon === "delete") {
                    return null;
                  } else if (el.icon === "check") {
                    const obj = item.isdefault
                      ? { color }
                      : { visibility: "hidden" };
                    return (
                      <Menu.Item key={i}>
                        <span className="ListCell_menuItem">
                          <Icon
                            style={{
                              ...obj,
                              fontSize: "16px",
                              marginRight: "10px",
                            }}
                            type={el.icon}
                          />
                          {el.name}
                        </span>
                      </Menu.Item>
                    );
                  } else {
                    return (
                      <Menu.Item key={i}>
                        <span className="ListCell_menuItem">
                          <Icon
                            style={{ fontSize: "16px", marginRight: "10px" }}
                            type={el.icon}
                            theme="filled"
                          />
                          {el.name}
                        </span>
                      </Menu.Item>
                    );
                  }
                } else {
                  return null;
                }
              })}
            </Menu>
          );
        }

        return (
          <div className="PlaylistCell" style={style}>
            <Dropzone
              noClick
              onDrop={(files) => {
                acceptedFiles && acceptedFiles(item, files);
              }}
              accept="image/bmp,image/gif,image/jpeg,image/png,video/mp4,video/avi,video/m3u8,video/flv,video/rmvb"
            >
              {({ getRootProps, getInputProps, isDragActive }) => {
                return (
                  <div
                    {...getRootProps()}
                    style={framePlay ? {} : { cursor: "pointer" }}
                    className={`list_cell ${
                      os().isPhone || os().isAndroid || os().isTablet
                        ? "noPc"
                        : ""
                    } ${get(item, "check") ? "list_cell_check" : ""}`}
                    onClick={(e) => {
                      if (selectNum) {
                        oncheck && oncheck(e, item.id, item.frame_default);
                      } else {
                        goPlaylistdetails &&
                          goPlaylistdetails(item);
                      }
                    }}
                  >
                    {isDragActive && acceptedFiles && (
                      <div className="DropUpList_box">
                        <input {...getInputProps()} />
                        <p>{title}</p>
                      </div>
                    )}

                    {oncheck && (
                      <span
                        style={
                          os().isPhone || os().isAndroid || os().isTablet
                            ? { visibility: "inherit" }
                            : {}
                        }
                        className={`icon-check-s treeNodeUnselectable`}
                        onClick={(e) => {
                          oncheck && oncheck(e, item.id, item.frame_default);
                        }}
                      >
                        <i
                          className="iconfont"
                          style={
                            get(item, "check") ? {} : { visibility: "hidden" }
                          }
                        >
                          &#xe62d;
                        </i>
                      </span>
                    )}

                    <div className="top_img">
                      {album ? (
                        <div
                          className="album_top_img"
                          style={
                            item.profile
                              ? {}
                              : {
                                  backgroundImage: `url(${imgload2})`,
                                  backgroundSize: "100% 100%",
                                }
                          }
                        >
                          {item.profile && (
                            <img
                              src={getFile.file_s(item.profile.id)}
                              alt={item.profile.id}
                            />
                          )}
                        </div>
                      ) : (
                        <>
                          <div
                            className="top_topImg"
                            style={
                              item.profile
                                ? {}
                                : {
                                    backgroundImage: `url(${imgload2})`,
                                    backgroundSize: "100% 140%",
                                  }
                            }
                          >
                            {item.profile && (
                              <img
                                src={getFile.file_s(item.profile.id)}
                                alt=""
                              />
                            )}
                          </div>
                          <div className="top_bottomImgs">
                            <span
                              className="img1"
                              style={
                                item.profile_1
                                  ? {}
                                  : {
                                      backgroundImage: `url(${imgload2})`,
                                      backgroundSize: "100% 100%",
                                    }
                              }
                            >
                              {item.profile_1 && (
                                <img
                                  src={getFile.file_s(item.profile_1.id)}
                                  alt=""
                                />
                              )}
                            </span>
                            <span
                              className="img2"
                              style={
                                item.profile_2
                                  ? {}
                                  : {
                                      backgroundImage: `url(${imgload2})`,
                                      backgroundSize: "100% 100%",
                                    }
                              }
                            >
                              {item.profile_2 && (
                                <img
                                  src={getFile.file_s(item.profile_2.id)}
                                  alt=""
                                />
                              )}
                            </span>
                            <span
                              className="img3"
                              style={
                                item.profile_3
                                  ? {}
                                  : {
                                      backgroundImage: `url(${imgload2})`,
                                      backgroundSize: "100% 100%",
                                    }
                              }
                            >
                              {item.profile_3 && (
                                <img
                                  src={getFile.file_s(item.profile_3.id)}
                                  alt=""
                                />
                              )}
                            </span>
                          </div>
                        </>
                      )}
                    </div>
                    <div className="information">
                      <div className="information_top">
                        <Tooltip
                          placement="bottom"
                          overlayClassName="tooltip"
                          title={item.name}
                        >
                          <p
                            className="name ellipsis"
                            style={
                              album
                                ? { width: "100%" }
                                : !item.frame_default
                                ? framePlay
                                  ? { width: "85%" }
                                  : { width: "100%" }
                                : {}
                            }
                          >
                            {name || item.name}
                          </p>
                        </Tooltip>
                        <p className="time">
                          <span className="time-span">
                            {timeName}
                            {dayjs(time || ti).format(t("L"))}
                          </span>
                          {item.isdefault && (
                            <span className="frames_isdefault">
                              {t("default")}
                            </span>
                          )}
                        </p>
                        {framePlay ? (
                          <Tooltip
                            title={t("playtoframe")}
                            overlayClassName="tooltip"
                          >
                            <span
                              className="frame-play"
                              onClick={(e) => {
                                framePlay(e, item.id);
                              }}
                            >
                              <Icon type="play-square" theme="filled" />
                            </span>
                          </Tooltip>
                        ) : (
                          item.frame_default > 0 && (
                            <span className="frames_def">{t("default")}</span>
                          )
                        )}
                      </div>
                      <div className="information_bottom">
                        <div className="information_bottom2">
                          <div className="photo-num">
                            <p className="num1">
                              {item.filecount ? item.filecount : 0}
                            </p>
                            <p className="text1">{t("Photo")}</p>
                          </div>
                          {!frameNumDis && (
                            <div className="frame-num">
                              <p className="num1">{item.frame_count}</p>
                              <p className="text1">{t("photoframe")}</p>
                            </div>
                          )}
                          {morelist && <div className="more">
                            <Dropdown
                              overlay={menu}
                              placement="bottomRight"
                              trigger={["hover", "click"]}
                            >
                              <Icon
                                onClick={(event) => event.stopPropagation()}
                                type="ellipsis"
                                style={{ fontSize: "20px" }}
                              />
                            </Dropdown>
                          </div>}
                        </div>
                      </div>
                    </div>
                  </div>
                );
              }}
            </Dropzone>
          </div>
        );
      }
    } catch (error) {
      // console.log(error)
      return <div style={style}></div>;
    }
  }
}

export default ListCell;
