import React, { Component } from 'react';
import { Modal } from 'antd';
import { withTranslation } from 'react-i18next';

@withTranslation()
class Control extends Component {
  render() {
    const { t, visible, onCancel, controlBtn, click, node } = this.props;
    return (
      <Modal
        visible={visible}
        title={t('control')}
        width={585}
        bodyStyle={{ textAlign: 'center' }}
        onCancel={onCancel}
        getContainer={false}
        centered={true}
        footer={null}
      >
        <div className='control'>
          <div className='control-body'>
            {controlBtn.map(item => {
              return <span key={item.id} className={item.class} onClick={() => { click(item.class, node) }}></span>
            })}
          </div>
        </div>
      </Modal>
    );
  }
}

export default Control;