import React, { Component } from 'react';
import { Icon } from 'antd';
import './index.scss';


/**
 *
 * @param {fn} go 
 * @param {string} name  
 */

export default class Goblack extends Component {
  render() {
    return (
      <div className='go-back' style={this.props.style}>
        <p className='go-back-on'>
          <span className='playlist-name ellipsis' onClick={this.props.go}><Icon type="left" />{this.props.name}</span>
        </p>
      </div>
    );
  }
}