import React, { Component } from "react";
import { Icon, Dropdown, Menu, Button } from "antd";
import "./index.scss";
import { withTranslation } from "react-i18next";
import Sorter from "./Sorter";

@withTranslation()
class Operation extends Component {
  state = {
    inner: false,
  };

  componentDidMount() {
    this.resize();
    window.addEventListener("resize", this.resize);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.resize);
  }

  resize = () => {
    if (window.innerWidth < 920) {
      if (!this.state.inner) {
        this.setState({ inner: true });
      }
    } else {
      if (this.state.inner) {
        this.setState({ inner: false });
      }
    }
  };

  render() {
    let items;
    const {
      t,
      style,
      id,
      selectNum,
      dataNum,
      oncheck,
      invert,
      moveTop,
      moveLast,
      remove,
      toPlace,
      sort,
      defaultSort,
      sortList,
      isdes,
      changeIndex,
      sortDis,
      sortEldis,
      cancelAll,
    } = this.props;
    if (toPlace) {
      items = (
        <Menu onClick={this.props.toPlaceClick}>
          {this.props.toPlace.map((item, i) => {
            return (
              <Menu.Item key={i}>
                <Icon type="plus" />
                {item.name}
              </Menu.Item>
            );
          })}
        </Menu>
      );
    }

    return (
      <div className="operation" style={style}>
        <div className="checkAll">
          <i
            className={`iconfont treeNodeUnselectable ${
              dataNum === selectNum && dataNum > 0
                ? "icon-xuanzhong1"
                : "icon-weixuanzhong"
            }`}
            onClick={oncheck}
          ></i>
          <span className="checkAll-text">
            {selectNum > 0
              ? t("Selected2").format(selectNum)
              : t("Allelection")}
          </span>
          {selectNum > 0 && invert ? (
            this.state.inner ? (
              <Button
                onClick={invert}
                className="cancel"
                style={{ height: "26px" }}
              >
                <i className="iconfont icon-fanxuan"></i>
              </Button>
            ) : (
              <Button style={{ padding: "0 8px" }} onClick={invert} className="invert-Selection invert btn">
                <i
                  className="iconfont icon-fanxuan"
                  style={{ marginRight: "8px" }}
                ></i>
                {t("Reverseselection")}
              </Button>
            )
          ) : null}
          {selectNum > 0 && cancelAll ? (
            this.state.inner ? (
              <Button
                icon="close"
                onClick={() => {
                  cancelAll && cancelAll();
                }}
                style={{ height: "26px" }}
                className="cancel"
              />
            ) : (
              <Button
                icon="close"
                onClick={() => {
                  cancelAll && cancelAll();
                }}
                className="invert-Selection btn"
                style={{ padding: "0 8px" }}
              >
                {t("Cancel")}
              </Button>
            )
          ) : null}
        </div>
        {sort && (
          <Sorter
            className="btn"
            defaultSort={defaultSort || {}}
            sortList={sortList}
            isdes={isdes}
            changeIndex={changeIndex}
            sortDis={sortDis}
            sortEldis={sortEldis}
          />
        )}
        {selectNum > 0 && moveTop && (
          <Button
            className="btn"
            onClick={() => {
              moveTop && moveTop(id);
            }}
            style={{ padding: "0 8px" }}
          >
            {this.state.inner ? (
              <i className="iconfont icon-xiangqianbupai"></i>
            ) : (
              <>
                <i className="iconfont icon-xiangqianbupai" style={{ marginRight: "8px" }}></i>
                {t("Movefront")}
              </>
            )}
          </Button>
        )}
        {selectNum > 0 && moveLast && (
          <Button
            className="btn"
            onClick={() => {
              moveLast && moveLast(id);
            }}
            style={{ padding: "0 8px" }}
          >
            {this.state.inner ? (
              <i className="iconfont icon-xianghoubupai"></i>
            ) : (
              <>
                <i className="iconfont icon-xianghoubupai" style={{ marginRight: "8px" }}></i>
                {t("Movelast")}
              </>
            )}
          </Button>
        )}
        {toPlace && selectNum > 0 && (
          <Dropdown overlay={items} trigger={["click"]}>
            <Button
              className="btn"
              style={this.state.inner ? { padding: "0 8px" } : {}}
            >
              <Icon type="plus" />
              {this.state.inner ? null : (
                <>
                  {t("addto")} <Icon type="down" />
                </>
              )}
            </Button>
          </Dropdown>
        )}
        {remove && selectNum > 0 && (
          <Button
            className="btn"
            icon="delete"
            onClick={() => {
              remove(id);
            }}
            style={{ padding: "0 8px" }}
          >
            {this.state.inner ? null : t("delete")}
          </Button>
        )}
      </div>
    );
  }
}

export default Operation;
