import React, { Component } from 'react';
import { Icon, message } from 'antd';
import { withTranslation } from 'react-i18next';
import './style.scss'
import { trim } from '../../util/index';

@withTranslation()
class Checkbox extends Component {
  constructor(props) {
    super(props);
    this.inputRef = React.createRef();
    this.state = {
      value: this.props.defaultValue,
      name: ''
    }
  }

  componentDidMount() {
    this.inputRef.current.focus();
  }

  handleChange = (event) => {
    this.setState({ value: event.target.value });
  }

  handleOK = () => {
    let val = trim(this.state.value);
    if (val !== '') {
      this.props.handleconfirm(val);
    }else{
      message.error(this.props.t('enterName'))
    }
  }

  render() {
    const { t, title, handlecancel,style } = this.props;
    return (
      <div className='custom-modal' style={style}>
        <div className='modal-warp'>
          <div className='modal-warp-header'>
            <h3>{title}</h3>
            <span onClick={handlecancel}>
              <Icon type="close" />
            </span>
          </div>
          <div className='modal-warp-boby'>
            <div className='modal-warp-input'>
              <input ref={this.inputRef} maxLength={50} autoFocus spellCheck={false} type="text" value={this.state.value} onChange={this.handleChange} />
            </div>
            <div className='modal-warp-handle'>
              <span className='cancel-btn' onClick={handlecancel}>{t('Cancel')}</span>
              <span
                className='confirm-btn'
                onClick={this.handleOK}>{t('Determine')}</span>
            </div>
          </div>
        </div>
      </div>
    );
  }
};

export default Checkbox;
