import React from "react";
import { withTranslation } from "react-i18next";
import { message, Icon } from "antd";
import dayjs from "dayjs";
import { inject, observer } from "mobx-react";
import { strRep } from "../../util/index";
import Loading from "../../common/Widget/Loading";
import Nodata from "../../common/Widget/Nodata";
import "./style.scss";
import { get, observable } from "mobx";
import Selectedlist from "../FrameOperate/selectedlist";
import Frameuser from "../FrameOperate/frameuser";
// import Information from '../../common/Information'
import { wsget } from "../../ws";
import Goblack from "../../common/Goblack";
import Control from "./Control";
import ListCell from "../../common/ListCell";
import Information from "../../common/Information";

@withTranslation()
@inject("rahmen", "playlist")
@observer
class Rahmendetails extends React.Component {
  constructor(props) {
    super(props);
    this.boxRef = React.createRef();
    this.state = {
      init: false,
      sel_visible: false,
      user_visible: false,
      menuDisplayId: null,
    };
  }
  @observable inner = false;
  @observable node = null;
  @observable name = null;
  @observable serial = null;
  @observable control_visible = false;
  @observable controlBtn = [
    { id: 1, class: "close" },
    { id: 2, class: "return" },
    { id: 3, class: "family" },
    { id: 4, class: "top" },
    { id: 5, class: "bottom" },
    { id: 6, class: "left" },
    { id: 7, class: "right" },
    { id: 8, class: "middle" },
    { id: 9, class: "pic" },
    { id: 10, class: "mute" },
    { id: 11, class: "menu" },
  ];
  @observable morelist = [
    { icon: "check", name: this.props.t("SetDefaultPlaylist") },
    { icon: "delete", name: this.props.t("delete") },
  ];
  @observable infoObj = null; //详情数据
  @observable info_visible = false; //详情显隐

  async componentDidMount() {
    try {
      const { id } = this.props.match.params;
      const { name, bindtime, is_admin, serial } = this.props.rahmen.urlData;
      this.node = Number(id);
      if (!name) {
        const rahmenArr = await wsget("frame_list", null);
        const pos = rahmenArr.findIndex((item) => item.node === Number(id));
        if (pos === -1) {
          this.props.history.push("/frame");
        }
        this.name = rahmenArr[pos].name;
        this.bindtime = rahmenArr[pos].bindtime;
        this.is_admin = rahmenArr[pos].is_admin;
        this.serial = rahmenArr[pos].serial;
      } else {
        this.name = name;
        this.bindtime = bindtime;
        this.is_admin = is_admin;
        this.serial = serial;
      }
      let cb = () => this.setState({ init: true });
      this.props.rahmen.rahmen_playlist_init(
        this.node,
        cb,
        this.boxRef.current.offsetWidth
      );
      if (this.is_admin) {
        this.props.rahmen.get_user_list(this.node);
      }
      window.addEventListener("resize", this.getWH);
      document.addEventListener("click", this.hide);
    } catch (error) {
      this.props.history.push("/frame");
    }
  }

  componentWillUnmount() {
    document.removeEventListener("click", this.hide);
    window.removeEventListener("resize", this.getWH);
    this.props.rahmen.reset0();
  }

  getWH = () => {
    this.getInner();
    this.props.rahmen.resize(
      this.boxRef.current.offsetWidth,
      this.props.rahmen.playList,
      "play"
    );
  };

  getInner = () => {
    if (window.innerWidth < 800) {
      if (!this.inner) {
        this.inner = true;
      }
    } else {
      if (this.inner) {
        this.inner = false;
      }
    }
  };

  // 更多/排序modal隐藏
  hide = () => {
    this.setState({ menuDisplayId: null });
  };

  //更多modal显示
  more_show = (e, node) => {
    e.nativeEvent.stopImmediatePropagation();
    this.setState({ menuDisplayId: node });
  };

  //选择列表显示
  selectedlist = () => {
    this.setState({ sel_visible: true });
  };

  //选择列表隐藏
  selectedlistHide = () => {
    this.setState({ sel_visible: false, setNode: null });
    this.props.rahmen.resetPlayitems();
  };

  //用户列表隐藏
  frameuserHide = () => {
    this.setState({ user_visible: false });
  };

  //设置默认列表
  set_default = (node, playlist_id) => {
    this.props.rahmen.set_default(node, playlist_id);
  };

  //删除列表
  delete = (item) => {
    if (item.isdefault) {
      message.warning(this.props.t("defaultNotDelete"));
    } else {
      this.props.rahmen.frame_playlist_delete(this.node, item.id);
    }
  };

  //相框播放
  framePlay = (e, node, id) => {
    e.stopPropagation();
    this.props.rahmen.frame_play(node, id);
  };

  goblack = () => {
    this.props.history.goBack();
  };

  showControl = () => {
    this.control_visible = true;
  };

  handleCancel = () => {
    this.control_visible = false;
  };

  info_show = () => {
    const infoObj = [
      {
        id: 1,
        icon: "unordered-list",
        txt: "playlist",
        data: this.state.init ? this.props.rahmen.playList.length : 0,
      },
      {
        id: 2,
        icon: "file-image",
        txt: "Photo",
        data: this.props.rahmen.sumPhoto,
      },
      {
        id: 3,
        icon: "trademark",
        txt: "SN",
        data: this.serial && strRep(this.serial),
      },
      {
        id: 4,
        icon: "clock-circle",
        txt: "bindingTime",
        data: dayjs(this.bindtime).format(this.props.t("LL")),
      },
    ];
    this.info_visible = true;
    this.infoObj = infoObj;
  };

  click = (val, item) => {
    val.domEvent.stopPropagation();
    if (val.key === "0") {
      this.set_default(this.node, item.id);
    } else if (val.key === "1") {
      this.delete(item);
    }
  };

  //跳转列表详情
  goPlaylistdetails = ({ id, name, created, frame_count }) => {
    this.props.playlist.goUrl(name, created, frame_count);
    this.props.history.push(`/playlist/detail/${id}`);
  };

  render() {
    const { t } = this.props;
    const { init, sel_visible, user_visible } = this.state;
    const {
      node,
      name,
      bindtime,
      is_admin,
      control_visible,
      infoObj,
      info_visible,
    } = this;
    const { playList, sumPhoto, telecontrol } = this.props.rahmen;
    let RahmenplaylistEl = (playList) => {
      if (init) {
        if (playList.length > 0) {
          return playList.map((item) => {
            return (
              <ListCell
                key={item.id}
                item={item}
                name={get(item, "name")}
                timeName={`${t("LastUpdate")}：`}
                time={item.updated || item.created}
                style={{ width: item.w, height: item.h, cursor: 'pointer' }}
                morelist={this.morelist}
                click={this.click}
                goPlaylistdetails={this.goPlaylistdetails}
                framePlay={(e, id) => {
                  this.framePlay(e, node, id);
                }}
              />
            );
          });
        } else {
          return <Nodata />;
        }
      } else {
        return <Loading />;
      }
    };

    return (
      <div className="rahmendetails">
        <div className="Rahmendetails-header">
          <Goblack go={this.goblack} name={name} />
          <div className="Rahmendetails-information">
            <div className="Rahmendetails-addphoto-btn">
              <button onClick={this.selectedlist}>{t("SelecList")}</button>
            </div>
            <div className="file-num">
              <div className="Rahmendetails-h-icon">
                <i className="iconfont icon">&#xe605;</i>
              </div>
              <div className="num-text">
                <p className="text">{t("playlist")}</p>
                <p className="num">{init ? playList.length : 0}</p>
              </div>
            </div>
            <div className="file-num">
              <div className="Rahmendetails-h-icon">
                <Icon type="file-image" />
              </div>
              <div className="num-text">
                <p className="text">{t("Photo")}</p>
                <p className="num">{sumPhoto}</p>
              </div>
            </div>
            <div className="creation-time">
              <div className="Rahmendetails-h-icon">
                <i className="iconfont icon">&#xe932;</i>
              </div>
              <div className="num-text">
                <p className="text">SN</p>
                <p className="num">{this.serial && strRep(this.serial)}</p>
              </div>
            </div>
            <div className="creation-time">
              <div className="Rahmendetails-h-icon">
                <Icon type="clock-circle" />
              </div>
              <div className="num-text">
                <p className="text">{t("bindingTime")}</p>
                <p className="num">{dayjs(bindtime).format(t("LL"))}</p>
              </div>
            </div>
            <div className="rahmen-operation">
              <div className="rahmen-info" onClick={this.info_show}>
                <Icon type="exclamation" />
              </div>
              {is_admin && (
                <div className="rahmen-tool" onClick={this.showControl}>
                  <i className="iconfont icon">&#xe783;</i>
                </div>
              )}
              {is_admin && (
                <div
                  className="rahmen-user"
                  onClick={() => {
                    this.setState({ user_visible: true });
                  }}
                >
                  <Icon type="team" />
                </div>
              )}
            </div>
          </div>
        </div>

        <div ref={this.boxRef} className="Rahmendetails-main">
          {RahmenplaylistEl(playList)}
        </div>

        {sel_visible ? (
          <Selectedlist node={node} cancel={this.selectedlistHide} />
        ) : null}

        <Frameuser
          visible={user_visible}
          node={node}
          cancel={this.frameuserHide}
          inner={this.inner}
        />

        <Control
          visible={control_visible}
          onCancel={this.handleCancel}
          controlBtn={this.controlBtn}
          click={telecontrol}
          node={this.node}
        />

        <Information
          visible={info_visible}
          data={infoObj}
          handleCancel={() => {
            this.info_visible = false;
          }}
        />
      </div>
    );
  }
}

export default Rahmendetails;
