import React, { Component } from "react";
import { Icon } from "antd";
import { inject, observer } from "mobx-react";
import { withTranslation } from "react-i18next";
import "./style.scss";
import { selfAdaption2, os } from "../../util/index";
import Nodata from "../../common/Widget/Nodata";
import Loading from "../../common/Widget/Loading";
import { get, observable } from "mobx";
import SmallPlaylist from "../../common/SmallPlaylist";
import Operation from "../../common/Operation";

@withTranslation()
@inject("rahmen", "playlist")
@observer
class Selectedlist extends Component {
  constructor(props) {
    super(props);
    this.listBoxRef = React.createRef();
  }
  @observable listW = 0;

  componentDidMount() {
    this.props.rahmen.selectedlist(() => {
      this.listW = selfAdaption2(this.listBoxRef.current.offsetWidth);
    });

    window.addEventListener("resize", this.resize);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.resize);
  }

  resize = () => {
    this.listW = selfAdaption2(this.listBoxRef.current.offsetWidth);
  };

  //确定
  handleOk = () => {
    this.props.rahmen.add_playlist(this.props.node);
    this.props.cancel();
  };

  render() {
    const { t } = this.props;
    const { playitems, Check, allCheck, seletplay } = this.props.rahmen;
    return (
      <div className="Setrahmen-moudle">
        <div className="Setrahmen-moudle-boby">
          <div className="Setrahmen-hd">
            <span className="hd-title">{t("SelecList")}</span>
            <span className="hd-close-icon" onClick={this.props.cancel}>
              <Icon type="close" />
            </span>
          </div>
          {playitems && (
            <Operation
              selectNum={playitems.length}
              dataNum={seletplay.length}
              oncheck={allCheck}
            />
          )}
          <div className="Setrahmen-bd" ref={this.listBoxRef} style={os().isPhone || os().isAndroid || os().isTablet ? {padding:0} : {}}>
            {playitems ? (
              playitems.length > 0 ? (
                playitems.map((item) => {
                  return (
                    <SmallPlaylist
                      key={item.id}
                      listW={this.listW}
                      item={item}
                      pitchOn={(item) => {
                        Check(item.id);
                      }}
                      check={get(item, "check")}
                    />
                  );
                })
              ) : (
                <Nodata />
              )
            ) : (
              <Loading />
            )}
          </div>
          <div className="Setrahmen-ft">
            <span className="cancel-btn" onClick={this.props.cancel}>
              {t("Cancel")}
            </span>
            <span className="confirm-btn" onClick={this.handleOk}>
              {t("Determine")}
            </span>
          </div>
        </div>
      </div>
    );
  }
}

export default Selectedlist;
